<template>
  <!-- 最外层，目前想法是添加一个背景图片做大背景 -->
  <div class="vulan">
    <!-- 最上面，想法是做一个导航栏，浏览器定位。 -->
    <div class="vulcanTop">
      <!-- logo -->
      <div class="logo">vulcan</div>
      <!-- 最左边，想法是弄个rpg游戏的角色面板，一个人物立绘，一个数值条 -->
      <div class="vulcanPlayer">
        <!-- 立绘图片 -->
        <a-avatar
          class="vulcanPlayerImg"
          :src="this.playerImg"
          shape="square"
          oncontextmenu="return false"
          ondragstart="return false"
        />
        <!-- 数值条1.0版本 -->
        <div class="vulcanPlayerNumerical">
          <p class="playerName">
            {{ playerName }}&nbsp;&nbsp;lv{{ playerLevel }}
          </p>
          <hr />
          <p class="titleHP">HP</p>
          <a-progress
            class="HP"
            strokeColor="#c4032f"
            status="normal"
            :percent="finalHP"
          />
          <p class="titleHP">MP</p>
          <a-progress
            class="MP"
            strokeColor="#001de0"
            status="normal"
            :percent="finalMP"
          />
        </div>
      </div>
      <!-- 中间 -->
      <div class="vulcanTopCenter">
        <!-- 导航开始喽 -->
        <a-menu class="vulcanNavigation" v-model="current" mode="horizontal">
          <a-menu-item key="这个是标识">不知道导航到哪里1</a-menu-item>
          <a-menu-item key="这个是标识2">不知道导航到哪里2</a-menu-item>
          <a-menu-item key="这个是标识3">不知道导航到哪里3</a-menu-item>
        </a-menu>
      </div>
      <!-- 最右侧 那当然是微型用户信息模块 -->
      <div class="vulcanUser">
        <div class="userName">
          {{ userName }}<br />
          <a-tooltip placement="bottom">
            <template slot="title">
              <span>点我进入个人中心</span>
            </template>
            <a class="userCenter">用户中心</a>
          </a-tooltip>
        </div>
        <div style="padding: 8px 0px; margin-left: 8px">
          <a-avatar
            oncontextmenu="return false"
            ondragstart="return false"
            :src="this.userImg"
            :size="64"
          />
        </div>
      </div>
    </div>
    <vulcan-body></vulcan-body>
  </div>
</template>

<script>
import vulcanBody from "./vulcanBody.vue";
export default {
  name: "index",
  components: { vulcanBody },
  props: [],
  data() {
    return {
      userImg: require("../assets/userImg.png"),
      userName: "许澍",
      playerImg: require("../assets/playerImg.jpg"),
      playerName: "许澍",
      playerLevel: 5,
      MaxHP: 100,
      HP: 80,
      MaxMP: 100,
      MP: 100,
      current: ["这个是标识"],
    };
  },
  computed: {
    //   计算当前血量
    finalHP() {
      return (this.HP / this.MaxHP) * 100;
    },
    // 计算当前发魔法值
    finalMP() {
      return (this.MP / this.MaxMP) * 100;
    },
  },
  methods: {},
};
</script>

<style scoped>
/* 通用样式 */
a {
  /*去掉下划线*/
  text-decoration: none;
}
hr {
  margin: 0;
}
p {
  margin: 0;
  padding: 0;
}
.vulan {
  width: 100%;
  height: 100%;
  /* 禁止复制文本 */
  user-select: none;
}
/* 最上层样式 */
.vulcanTop {
  width: 100%;
  height: 80px;
  background-color: rgb(92, 2, 177);
  display: flex;
  position: fixed;
  top: 0;
}
.logo {
  width: 7%;
  height: 100%;
  color: white;
  font-size: 17px;
  font-weight: 800;
  padding-top: 23px;
}
/* 角色面板 */
.vulcanPlayer {
  display: flex;
  margin: 0;
  width: 16%;
  height: 80px;
  background-color: #ba91fc;
  border: 2px solid #3e0275;
  border-radius: 16px;
  overflow: hidden;
}
.vulcanPlayerImg {
  width: 40%;
  height: 100%;
  text-align: center;
}
.vulcanPlayerNumerical {
  width: 60%;
  height: 100%;
  background-color: #ba91fc;
  margin-left: -3px;
}
.playerName {
  text-align: left;
  font-size: 18px;
  height: 21px;
  font-family: "STXinwei";
  padding-left: 5px;
}
.titleHP {
  margin: -2px 0 1px 0;
  padding: 0;
  text-align: left;
  padding-left: 9px;
  width: 100%;
  height: 5px;
  font-size: 12px;
  font-weight: 600;
}
.HP {
  width: 90%;
  font-size: 12px;
  font-family: fantasy;
  margin: 0;
}
.MP {
  width: 90%;
  font-size: 12px;
  font-family: fantasy;
  margin: 0;
}
/* 顶部中间部分 */
.vulcanTopCenter {
  width: 60%;
}
/* top导航条 */
.vulcanNavigation {
  width: 100%;
  height: 60%;
  background: rgb(92, 2, 177);
  color: white;
  font-size: 22px;
  font-family: fangsong;
  margin: 0;
  padding: 0px;
}
.vulcanNavigation :hover {
  color: rgb(180, 146, 245);
  border-color: rgb(92, 2, 177);
}

.ant-menu-item-selected {
  color: white;
  border-color: rgb(92, 2, 177);
}
.ant-menu-item-active {
  color: rgb(133, 71, 248);
  border-color: rgb(92, 2, 177);
}
/* 微型用户界面 */
.vulcanUser {
  height: 100%;
  display: flex;
}
.userName {
  width: 100%;
  height: 100%;
  padding: 12px 2px;
  font-size: 14px;
  font-weight: 600;
  color: white;
}
.userCenter {
  width: 100%;
  height: 100%;
  font-size: 18px;
  font-weight: 800;
  font-family: fangsong;
  color: white;
  border: 2px solid white;
  border-radius: 8px;
  padding: 3px;
  background: mediumvioletred;
  overflow: hidden;
}
</style>



